* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: content-box;
}
body {
  padding: 0 15px;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}
a {
  color: inherit;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.title {
  font-size: 32px;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
}
.add-form {
  margin: 0 auto;
  margin-top: 50px;
  border: 1px solid black;
  border-radius: 5px;
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.add-label {
  align-self: flex-start;
  margin-left: 50px;
  font-size: 12px;
}
.filter-label {
  margin-right: 60px;
  font-size: 12px;
}
.add-input {
  margin-top: 1px;
  width: 200px;
  margin-bottom: 20px;
}
.filter-input {
  margin-top: 1px;
  width: 200px;
  margin-bottom: 20px;
}
.add-btn {
  color: rgb(247, 237, 237);
  width: 100px;
  height: 30px;
  border-radius: 5px;
  border: none;
  background-color: rgba(22, 160, 7, 0.715);
  transition: 200ms ease-in-out;
}
.add-btn:hover {
  background-color: rgb(7, 169, 20);
  box-shadow: 0 2px 10px rgb(149, 149, 149);
}
.delete-btn,
.user-menu-btn {
  color: rgb(247, 237, 237);
  width: 100px;
  height: 30px;
  border-radius: 5px;
  border: none;
  background-color: rgba(160, 7, 7, 0.715);
  transition: 200ms ease-in-out;
}
.delete-btn:hover,
.user-menu-btn:hover {
  background-color: rgb(169, 7, 7);
  box-shadow: 0 2px 10px rgb(149, 149, 149);
}
.contacts-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: center;
  gap: 15px;
  margin-top: 40px;
  margin-bottom: 50px;
}

.contacts-item {
  width: 90%;
  max-width: 500px;
  margin: 0 auto;
  border: 1px solid rgba(179, 179, 179, 0.676);
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 24px;
  display: flex;
  align-items: center;
}
.contacts-item-name {
  flex-grow: 1;
}
.contacts-item-number {
  margin-left: 10px;
  font-weight: 500;
}
.contacts-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
/** -----------------------------LOADER-----------------------------**/

.loader {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  display: inline-block;
  position: absolute;
  top: 47%;
  left: 47%;
  border: 8px solid;
  border-color: #000000 #010101 transparent;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 4px solid;
  border-color: transparent #ff3d00 #ff3d00;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  animation: rotationBack 0.5s linear infinite;
  transform-origin: center center;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

/** -----------------------------Final-----------------------------**/

.header-nav-list {
  margin: 0 100px 0 100px;
  padding: 10px 0;
  margin-top: 10px;
  margin-right: 100px;
  margin-left: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-nav-list-item {
  display: flex;
  gap: 40px;
}
.nav-item {
  padding: 5px 20px;
  border-radius: 5px;
  border: none;
  background-color: rgba(189, 189, 189, 0.715);
  transition: 200ms ease-in-out;
}
.nav-item:hover {
  background-color: rgb(184, 184, 184);
  box-shadow: 0 2px 10px rgb(176, 176, 176);
}
.user-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.register-wrapper,
.login-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  position: relative;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 600px;
  gap: 10px;
}

.user-menu-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
}
.user-menu-btn {
  width: 80px;
  height: 25px;
}
.redirect-text {
  font-size: 12px;
  position: relative;
  top: -15px;
}
.redirect-link {
  font-weight: 600;
  color: rgb(1, 1, 120);
}

/** -----------------------------HomePage-----------------------------**/

.homepage-wrapper {
  text-align: center;
}
.homepage-title {
  font-size: 36px;
  margin-bottom: 20px;
}
